import { AbstractLink, HTMLLinkTarget, HyperLinkParameter, HyperLinkProps } from './abstract.link';
import { openUrl } from '../hyper_link.service';
import { appendParams2Url } from '@engine/crud';

export class HTMLLink extends AbstractLink {
    protected _url: string;
    protected target: HTMLLinkTarget;
    protected width?: number;
    protected height?: number;
    protected parameters: HyperLinkParameter[];
    public constructor(props: HTMLLinkProps) {
        super(props);
        const { targetFrame, url, width, height, parameters = [] } = props;
        this.url = url;
        this.target = targetFrame;
        this.width = width;
        this.height = height;
        this.parameters = parameters;
    }

    protected _excute() {
        openUrl({
            target: this.target,
            url: this.url,
            width: this.width,
            height: this.height,
            title: this.name,
        });
    }

    protected get url() {
        const parameter = {} as {[key: string]: any};
        this.parameters.forEach(item => {
            parameter[item.name] = item.value;
        });
        
        return appendParams2Url(this._url, parameter);
    }

    protected set url(url) {
        this._url = url;
    }
}


/**
 * targetFrame 打开方式
 * url 链接
 * parameters 参数
 * width 弹窗宽
 * height 弹窗高
 */
export interface HTMLLinkProps extends HyperLinkProps {
    targetFrame: HTMLLinkTarget;
    url: string;
    parameters?: HyperLinkParameter[];
}
